<template>
    <div class="fnq-area">
        <div class="fnq-wrap">
            <div class="fnq-box">
                <ul class="fnq-list">
                    <li v-for="(qna, i) in this.qna" :key="i">
                        <span @click="toggle(i)" class="q" :class="{ active: this.slideUp[i] == true }">{{qna.question}}</span>
                        <transition name="accordion"
                            @before-enter="beforeEnter"
                            @enter="enter"
                            @before-leave="beforeLeave"
                        >
                            <span v-show="this.slideUp[i] == true" class="a" >{{qna.answer}}</span>
                        </transition>
                        
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name : 'Detail22',
    data(){
        return{
            slideUp : [],
            qna : [
                {
                    "id": "0",
                    "question" : "   .   ?",
                    "answer" : "       "
                },
                {
                    "id": "1",
                    "question" : " ?",
                    "answer" : "   /   ."
                },
                {
                    "id": "2",
                    "question" : "",
                    "answer" : "         ."
                },
                {
                    "id": "3",
                    "question" : "",
                    "answer" : "  , ,             ."
                },
                {
                    "id": "4",
                    "question" : " .",
                    "answer" : "          ,   ."
                },
                {
                    "id": "5",
                    "question" : "",
                    "answer" : "  , ,             ."
                },
                {
                    "id": "6",
                    "question" : " .",
                    "answer" : "          ,   ."
                },
            ],
        }
    },
    mounted(){
        let array = [];
        this.qna.forEach(function(){
            array.push(false);
        })
        this.slideUp = [...array];

    },
    methods:{
        toggle(index){
            let openArr = this.slideUp.map(e => {
                e = false;
                return e;
            })
            if(this.slideUp[index] == false){
                this.slideUp = [...openArr];
                this.slideUp[index] = true;
            }else{
                this.slideUp = [...openArr];
            }
        },
        beforeEnter(el) {
            el.style.height = '0rem';
            el.style.opacity = '0';
            el.style.transitionProperty = 'all';
            el.style.transitionDuration = '0.6s';
        },
        enter(el, done) {
            setTimeout(()=>{
                el.style.height = '4rem';
                el.style.opacity = '1';
            }, 100);
            done();

        },
        beforeLeave(el) {
            el.style.height = '0rem';
            el.style.opacity = '0'; 
        },
    }
}
</script>

<style lang="scss" >
@import '../assets/style/root.scss';

.fnq-area{
    @include layout;
}

.fnq-list{
    border-top: 1px solid #0e0e0e;
    border-bottom: 1px solid #0e0e0e;
    li ~ li{
        border-top: 1px solid #D1D1D1;
    }
}

.fnq-list li{
    span{
        @include centerVertical;
        overflow: hidden;
        width:100%;
        height:4rem;
        padding: 0 1.8rem;
    }
    .q{
        position: relative;
        cursor:pointer;
        &:before{
            @include bef;
            left:auto;
            top: 50%;
            right:1.8rem;
            width:0.4rem;
            height:0.667rem;
            background: url('../assets/icon_arrow_gr.png') no-repeat center center/cover;
            transform: translateY(-50%) rotate(90deg);
        }
    }
    .q.active{
        &:before{
            transform: translateY(-50%) rotate(-90deg);
        }
    }
    .a{
        background:#f3f3f3;
    }
    
}



</style>